import * as React from "react"
import { Link } from "gatsby"
import nav from "../data/nav"
import generic from "../data/generic"
import translateLink from "./logic/translateLink"
import { useStateMachine } from "little-state-machine"
import styles from "./Footer.module.css"

export default ({ currentLanguage }: { currentLanguage: string }) => {
  const { state } = useStateMachine()
  const lightMode = state?.setting?.lightMode

  return (
    <footer
      className={`${styles.footer} ${lightMode ? styles.lightFooter : {}}`}
    >
      <ul className={styles.links}>
        <li>
          <Link to={translateLink("/", currentLanguage)}>
            {nav[currentLanguage].home}
          </Link>
        </li>
        <li>
          <Link to={translateLink("/get-started", currentLanguage)}>
            {nav[currentLanguage].installation}
          </Link>
        </li>
        <li>
          <Link to={translateLink("/installation", currentLanguage)}>Installation</Link>
        </li>
        <li>
          <Link to={translateLink("/templates", currentLanguage)}>
            {nav[currentLanguage].templates}
          </Link>
        </li>
        <li>
          <Link to={translateLink("/examples", currentLanguage)}>
            {nav[currentLanguage].examples}
          </Link>
        </li>
      </ul>
      
      <p style={{ fontSize: 12 }}>
        [ {generic.support[currentLanguage]}{" "}
        <a
          href="https://github.com/react-hook-form/react-hook-form"
          target="_blank"
          rel="noreferrer noopener"
        >
          @github
        </a>
        ️ ]
      </p>
      <p
        style={{
          display: "inline-block",
          background: "white",
          color: "black",
          padding: "6px 20px",
          borderRadius: 4,
        }}
      >
        Hosted on GitHub Pages
      </p>
    </footer>
  )
}
